*{margin: 0;padding: 0;}

body {
  min-width: 300px;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
nav a.active{
  border-bottom-width: 2px;
}
li{
  list-style: none
}
a{
  text-decoration: none;
  color: #000;
}
.top{
  position: fixed;
  z-index: 100;
  top:0;
  width:100%
}
.nav{
  display: flex;
  flex-shrink: 1;
  background: #fff;
  border-bottom: 1px solid rgba(128, 128, 128, 0.158)
}
.nav a{
  display: block;
  width:34%;  
  height: 40px;
  line-height: 40px;
  box-sizing: border-box;
  overflow: hidden;
  border-bottom-style: solid;
  border-bottom-color:#ce3f2f; 
  border-bottom-width: 0;
  text-align: center;
  color: #333;
  font-size: 15px;
  text-decoration: none
}
.nav .active{
  border-bottom-width: 2px;
  color: #ce3f2f
}

.cont{
  margin-top: 100px
}



.header{
     position: relative;
    height:60px;
    background: #d43e2d url(https://coder-linshun.github.io/react_demo_wyymusic/img/log1.jpg) no-repeat left;
    background-size: auto 62px
}
.header a{
    display: block;
    position:absolute;
    top:17px;
    right:12px;
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    background: none;
    border:1px solid white;
    border-radius:15px;
    text-decoration: none;
    color: #fff;

    font-size: 15px;
    font-weight: 600
}
a{
    color: #333;
}
.cont_box{
    padding-top: 1px;
}
.cont_box .rec_t,
.cont_box .new_t{
    margin:20px 0 14px;
    padding-left: 9px;  
    height: 20px;
    border-left-color: #ce3f2f;
    border-left-style: solid;
    border-left-width: 3px;
    font: 17px/20px '\5FAE\8F6F\96C5\9ED1';
}
.rec_cont{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between
}
.rec_cont a{
    position: relative;
    display: block;
    margin-bottom: 18px;
    float: left;
    width: 32%;
    
}
.rec_cont a img{
    width:100%;
}

.rec_cont a p{
    overflow: hidden;
    padding: 6px 2px 0 6px;
    line-height: 1.2;
    font-size: 13px;
}
.rec_cont a span{
    position: absolute;
    right: 5px;
    top: 2px;
    z-index: 3;
    color: #fff;
    font-size: 12px; 
    text-shadow: 1px 0 0 rgba(0,0,0,.15);
}
.rec_cont a span i{
    display: inline-block;
    width:17px;
    height: 12px;
    background: url(https://coder-linshun.github.io/react_demo_wyymusic/img/iconall.png) no-repeat left -22px;
}
.new_cont{
    padding-left:10px;
}

.new_cont .new_item{
    position: relative;
    padding: 6px 0;
    display: block;
    border-bottom: 1px solid rgba(128, 128, 128, 0.158)

}
.new_cont .new_item div{
    width:80%
}
.new_cont .new_item .song_name{
    font-size: 17px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: normal;
}
.new_cont .new_item .singer_name{
    font-size: 12px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: normal;
    color: #888;
}
.new_cont .new_item .singer_name span{
    display: inline-block;
    width:16px;
    height:18px;
    background: url(https://coder-linshun.github.io/react_demo_wyymusic/img/index_icon_2x.png) no-repeat left 10px;
    background-size: 166px 97px;  
}
.new_cont .new_item .play_icon_r{
    display: block;
    position: absolute;
    right: 0;
    top:0;
    width:40px;
    height:56px;
    background: url(https://coder-linshun.github.io/react_demo_wyymusic/img/index_icon_2x.png) no-repeat -24px 16px;
    background-size: 166px 97px;  
}
footer{
    padding: 100px 0 70px;
    background: url(https://coder-linshun.github.io/react_demo_wyymusic/img/recommand_bg_2x.png) no-repeat;
    background-size: cover;
}
footer span{
    display: block;
    width:230px;
    height:44px;
    margin: 0 auto;
    background: url(https://coder-linshun.github.io/react_demo_wyymusic/img/log2.png) no-repeat;
    background-size: cover;
}
footer div{
    line-height: 38px;
    border: 1px solid #d33a31;
    border-radius: 38px;
    font-size: 16px;
    color: #d33a31;
    margin: 20px 37px 5px;
    text-align: center;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: normal;
}
footer p{
    
    color: #888;
    font-size: 12px;
    line-height: 16px;
    text-align: center;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: normal;
}
.warp .big_pic{
    padding: 20px 30px;
    height: 120px;
    background: url(https://coder-linshun.github.io/react_demo_wyymusic/img/hot_music_bg_2x.jpg) no-repeat ;
    background-size: 100% 100%
}
.warp .big_pic .title{
    width:160px;
    height: 90px;
    background: url(https://coder-linshun.github.io/react_demo_wyymusic/img/index_icon_2x.png) no-repeat -20px -20px;
    background-size: 160px auto;
  
}
.warp .big_pic .time{
    color: hsla(0,0%,100%,.8);
    font-size: 12px;
}
.warp .list .li{
    display: block;
    overflow: hidden;
    position: relative;
    height: 55px;
    padding-left: 10px;
}

.warp .list .li>div:first-child{
    position: absolute;
    z-index: 2;
    top:1px;
    width:28px;
    height: 100%;
    line-height: 55px;
    background: white;
    font-size: 17px;
   
    color: #999;
}
.warp .list .li:nth-of-type(1)>div:first-child,
.warp .list .li:nth-of-type(2)>div:first-child,
.warp .list .li:nth-of-type(3)>div:first-child{
    color: #df3436;
}
.warp .list .lists{
    height: 42px;
    padding: 6px 0;
    margin-left: 28px;
    border-bottom: 1px solid rgba(128, 128, 128, 0.158)
}
.warp .list .lists .song{
    font-size: 18px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: normal;
}
.warp .list .lists .singer{
    font-size: 12px;
    color: #888;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: normal;
}
.warp .list .lists .singer span{
    display: inline-block;
    width:16px;
    height:14px;
    background: url(https://coder-linshun.github.io/react_demo_wyymusic/img/index_icon_2x.png) no-repeat left 5px;
    background-size: 166px 97px; 
}

.play_icon_r{
    display: block;
    position: absolute;
    right: 0;
    top:0;
    width:40px;
    height:54px;
    background: url(https://coder-linshun.github.io/react_demo_wyymusic/img/index_icon_2x.png) no-repeat -24px 16px;
    background-size: 166px 97px;  
}
.search_warp form{
    padding: 15px 10px;
    position: relative;
    border-bottom: 1px solid rgba(128, 128, 128, 0.158)
}
.search_warp form input{
    position: relative;
    width: 100%;
    height: 30px;
    padding: 0 30px;
    outline: none;
    box-sizing: border-box;
    background: #ebecec url(https://coder-linshun.github.io/react_demo_wyymusic/img/search.png) no-repeat 7px 5px;
    background-size:20px auto;
    border:none;
    border-radius: 30px;
}
.play_box{
    position: fixed;
    z-index: 999;
    top:0;
    width: 100%;
    height:100%;
    background:  url(https://coder-linshun.github.io/react_demo_wyymusic/img/bg.jpg) no-repeat;
    background-size: cover;
}
.play_box .op_box{
    height: 100%;
    background-color: rgba(0,0,0,0.3)
}
.play_box .logo{
    position: absolute;
    top: 12px;
    left: 10px;
    display: block;
    height: 32px;
    width:140px;
    background:  url(https://coder-linshun.github.io/react_demo_wyymusic/img/log3.png) no-repeat;
    background-size: 140px auto;
}
.play_box .m-song-disc{
    position:relative;
    width: 70%;
    max-width: 400px;
    min-width: 300px;
    margin: 0 auto;
    top:90px;
    background: red
}
.play_box img{
    display: block;
    position: absolute
}
.play_box .m-song-disc .disc{
    width:100%;
    border-radius: 50%;
    /* box-shadow: 0 0 2px 12px rgba(255, 255, 255, 0.645) inset */

}
.play_box .m-song-disc .pic{
    box-sizing: border-box;
    border:12px solid rgba(255,255,255,0.05);
    border-radius: 50%;
    width:100%;
}
.play_box .m-song-disc .l{
    top:-90px;
    left: 43%;
     margin: 0 auto;
    width:40%;
    z-index: 9999;
}
.play_box .m-song-disc .icon{
    
    width:30%;
    margin-top:50%;
    left:50%;
    -webkit-transform: translateX(-50%)  translateY(-50%);
            transform: translateX(-50%)  translateY(-50%); 
}
.music_msg{
    position: fixed;
    z-index: 99999;
    padding: 10px;
    bottom: 0px;
    height:40px;
    width:100%;
    background: url(https://coder-linshun.github.io/react_demo_wyymusic/img/bg.jpg) no-repeat; 
    background-size: cover;
    font: 20px/40px '\5FAE\8F6F\96C5\9ED1';
    color: #fff;
    text-align: center;
    text-shadow: 1px 1px #ce3f2f;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: normal;
}
.music_msg span{
    color: rgb(233, 229, 229);
    text-shadow: none
    
}
 
