footer{
    padding: 100px 0 70px;
    background: url(https://coder-linshun.github.io/react_demo_wyymusic/img/recommand_bg_2x.png) no-repeat;
    background-size: cover;
}
footer span{
    display: block;
    width:230px;
    height:44px;
    margin: 0 auto;
    background: url(https://coder-linshun.github.io/react_demo_wyymusic/img/log2.png) no-repeat;
    background-size: cover;
}
footer div{
    line-height: 38px;
    border: 1px solid #d33a31;
    border-radius: 38px;
    font-size: 16px;
    color: #d33a31;
    margin: 20px 37px 5px;
    text-align: center;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: normal;
}
footer p{
    
    color: #888;
    font-size: 12px;
    line-height: 16px;
    text-align: center;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: normal;
}