.top{
  position: fixed;
  z-index: 100;
  top:0;
  width:100%
}
.nav{
  display: flex;
  flex-shrink: 1;
  background: #fff;
  border-bottom: 1px solid rgba(128, 128, 128, 0.158)
}
.nav a{
  display: block;
  width:34%;  
  height: 40px;
  line-height: 40px;
  box-sizing: border-box;
  overflow: hidden;
  border-bottom-style: solid;
  border-bottom-color:#ce3f2f; 
  border-bottom-width: 0;
  text-align: center;
  color: #333;
  font-size: 15px;
  text-decoration: none
}
.nav .active{
  border-bottom-width: 2px;
  color: #ce3f2f
}

.cont{
  margin-top: 100px
}


