a{
    color: #333;
}
.cont_box{
    padding-top: 1px;
}
.cont_box .rec_t,
.cont_box .new_t{
    margin:20px 0 14px;
    padding-left: 9px;  
    height: 20px;
    border-left-color: #ce3f2f;
    border-left-style: solid;
    border-left-width: 3px;
    font: 17px/20px '微软雅黑';
}
.rec_cont{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between
}
.rec_cont a{
    position: relative;
    display: block;
    margin-bottom: 18px;
    float: left;
    width: 32%;
    
}
.rec_cont a img{
    width:100%;
}

.rec_cont a p{
    overflow: hidden;
    padding: 6px 2px 0 6px;
    line-height: 1.2;
    font-size: 13px;
}
.rec_cont a span{
    position: absolute;
    right: 5px;
    top: 2px;
    z-index: 3;
    color: #fff;
    font-size: 12px; 
    text-shadow: 1px 0 0 rgba(0,0,0,.15);
}
.rec_cont a span i{
    display: inline-block;
    width:17px;
    height: 12px;
    background: url(https://coder-linshun.github.io/react_demo_wyymusic/img/iconall.png) no-repeat left -22px;
}
.new_cont{
    padding-left:10px;
}

.new_cont .new_item{
    position: relative;
    padding: 6px 0;
    display: block;
    border-bottom: 1px solid rgba(128, 128, 128, 0.158)

}
.new_cont .new_item div{
    width:80%
}
.new_cont .new_item .song_name{
    font-size: 17px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: normal;
}
.new_cont .new_item .singer_name{
    font-size: 12px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: normal;
    color: #888;
}
.new_cont .new_item .singer_name span{
    display: inline-block;
    width:16px;
    height:18px;
    background: url(https://coder-linshun.github.io/react_demo_wyymusic/img/index_icon_2x.png) no-repeat left 10px;
    background-size: 166px 97px;  
}
.new_cont .new_item .play_icon_r{
    display: block;
    position: absolute;
    right: 0;
    top:0;
    width:40px;
    height:56px;
    background: url(https://coder-linshun.github.io/react_demo_wyymusic/img/index_icon_2x.png) no-repeat -24px 16px;
    background-size: 166px 97px;  
}