.play_box{
    position: fixed;
    z-index: 999;
    top:0;
    width: 100%;
    height:100%;
    background:  url(https://coder-linshun.github.io/react_demo_wyymusic/img/bg.jpg) no-repeat;
    background-size: cover;
}
.play_box .op_box{
    height: 100%;
    background-color: rgba(0,0,0,0.3)
}
.play_box .logo{
    position: absolute;
    top: 12px;
    left: 10px;
    display: block;
    height: 32px;
    width:140px;
    background:  url(https://coder-linshun.github.io/react_demo_wyymusic/img/log3.png) no-repeat;
    background-size: 140px auto;
}
.play_box .m-song-disc{
    position:relative;
    width: 70%;
    max-width: 400px;
    min-width: 300px;
    margin: 0 auto;
    top:90px;
    background: red
}
.play_box img{
    display: block;
    position: absolute
}
.play_box .m-song-disc .disc{
    width:100%;
    border-radius: 50%;
    /* box-shadow: 0 0 2px 12px rgba(255, 255, 255, 0.645) inset */

}
.play_box .m-song-disc .pic{
    box-sizing: border-box;
    border:12px solid rgba(255,255,255,0.05);
    border-radius: 50%;
    width:100%;
}
.play_box .m-song-disc .l{
    top:-90px;
    left: 43%;
     margin: 0 auto;
    width:40%;
    z-index: 9999;
}
.play_box .m-song-disc .icon{
    
    width:30%;
    margin-top:50%;
    left:50%;
    transform: translateX(-50%)  translateY(-50%); 
}
.music_msg{
    position: fixed;
    z-index: 99999;
    padding: 10px;
    bottom: 0px;
    height:40px;
    width:100%;
    background: url(https://coder-linshun.github.io/react_demo_wyymusic/img/bg.jpg) no-repeat; 
    background-size: cover;
    font: 20px/40px '微软雅黑';
    color: #fff;
    text-align: center;
    text-shadow: 1px 1px #ce3f2f;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: normal;
}
.music_msg span{
    color: rgb(233, 229, 229);
    text-shadow: none
    
}
 