.header{
     position: relative;
    height:60px;
    background: #d43e2d url(https://coder-linshun.github.io/react_demo_wyymusic/img/log1.jpg) no-repeat left;
    background-size: auto 62px
}
.header a{
    display: block;
    position:absolute;
    top:17px;
    right:12px;
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    background: none;
    border:1px solid white;
    border-radius:15px;
    text-decoration: none;
    color: #fff;

    font-size: 15px;
    font-weight: 600
}